import React, { Component, PropTypes } from 'react';
import { Router, Route, Link ,hashHistory} from 'react-router';
/* input  */
import Input from '../../module/input/input';
/*  */
import { Picker, List,  Toast, WhiteSpace , WingBlank, InputItem} from 'antd-mobile';
/* form */
import { createForm } from 'rc-form';
/*  */
/*  */
import Title from '../../module/navTitle/navTitle';
/*  */
import Button from '../../../scss/button/button';


// start
import './safe.scss'
// end
var _that=null;
class Forms extends Component{
    constructor(){
        super();
    }
    componentWillMount(){
        _that=this;
        //
        this.state={
            nameValue:'',
            nameError:false,
            userValue:'',
            userError:false,
            bankValue:'',
            bankError:false,
            bankNumber:'',
            bankNumberError:false,
            data: [],
            cols: 1,
            pickerValue: [],
            asyncValue: []
        }
    }
    componentDidMount(){
        //  start
        componentStore.set(this);
        //  end
    };
    componentWillUnmount(){
        //  start
        componentStore.clear(this);
        //  end
    };
    toModule3(){
        Utils.switchRoute('/module3')
    };
    retreat(){
        alert('');
    };
    //
    nameBlur(){
        //console.log(_that)
        let name=this.value;
        if(!name){
            componentStore.update(_that,{
                nameError:true
            })
            Toast.info('', 1);
        }else{
            componentStore.update(_that,{
                nameValue:name //
            })
        }
    };
    nameFocus(){
        componentStore.update(_that,{
            nameError:false
        })
    };
    //
    userBlur(){
        let user=this.value;
        let text =new RegExp(/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/);
        let userV=text.test(user);
        if(!user){
            componentStore.update(_that,{
                userError:true
            })
            Toast.info('', 1);
        }else{
            if(!userV){
                componentStore.update(_that,{
                    userError:true
                })
                Toast.info('', 2);
            }else{
                componentStore.update(_that,{
                    userValue:user //
                })
            }
        }
    };
    userFocus(){
        componentStore.update(_that,{
            userError:false
        })
    }
    //
    onPickerChange (val) {//change
        //console.log(val);
        componentStore.update(_that,{
            bankValue:val //
        })
        let colNum = 1;
        const bank = [...this.state.data];
        const asyncValue = [...val];
        this.setState({
            data: bank,
            cols: colNum,
            asyncValue,
        });
    };
    onClick (){//
        var _this = this;
        Utils.requestData({
            url: config.mock_baseUrl + 'api/module1/test_search2',
            method: 'POST',
            data: {
                a: 1,
                b: 2
            },
            callback: function(data){
                console.log("data--:"+ JSON.stringify(data));
                componentStore.update(_this,{
                    data: data.data
                 })

                //console.log(data)
            }
         });
    };
    //
    bankBlur(){
        let bankVal=this.value;
        if(bankVal != undefined ){
            //console.log('1')
            if(bankVal.length<=15 || bankVal.length>19){
                componentStore.update(_that, {
                    bankNumberError: true
                })
                Toast.info('', 2);
            }else{
                componentStore.update(_that,{
                    bankNumber:bankVal //
                })
            }
        }else{
            componentStore.update(_that, {
                bankNumberError: true
            })
            Toast.info('', 2);
        }
    };
    banFocus(){
        componentStore.update(_that,{
            bankNumberError:false
        })
    }
    //
    safeClick(){
        //
        if(_that.state.nameValue=='' || _that.state.nameValue==undefined ){
            componentStore.update(_that,{
                nameError:true
            })
            Toast.info('', 1);
            return false;
        }
        //
        if(_that.state.userValue=='' || _that.state.userValue==undefined ){
            componentStore.update(_that,{
                userError:true
            })
            Toast.info('', 1);
            return false;
        }
        //
        if(_that.state.bankValue=='' || _that.state.bankValue==undefined ){
            Toast.info('', 1);
            return false;
        }
        //
        if(_that.state.bankNumber=='' || _that.state.bankNumber==undefined ){
            componentStore.update(_that,{
                bankNumberError:true
            })
            Toast.info('', 1);
            return false;
        }
        //console.log(""+_that.state.nameValue)
        //console.log(""+_that.state.userValue)
        //console.log(""+_that.state.bankValue)
        //console.log(""+_that.state.bankNumber)
        Toast.info('', 1);
    }
    render(){
        //console.log("nameValue"+this.state.nameValue)
        const { getFieldProps } = this.props.form;
        return (
             <div>
                 <Title $id="lys_form"
                        title=""
                        retreat={this.retreat}
                        grade="2"
                 >
                 </Title>
                {/*<Input $id="lys_input"  />*/}
                <div className="safe">
                    <div className="safe_title">
                        
                    </div>
                    <div className="safe_list">
                        <ul>
                            <li>
                                <div className="_left">
                                    <div className="_img">
                                        <img src={require('../../../image/icon/safe_name.png')} alt=""/>
                                    </div>
                                </div>
                                <div className="_right _rightLast">
                                    <div className="_right_C">
                                        <InputItem
                                            {...getFieldProps('userName')}
                                            type="text"
                                            placeholder=""
                                            onBlur={this.nameBlur}
                                            error={this.state.nameError}
                                            onFocus={this.nameFocus}
                                            value={this.state.nameValue}
                                            onChange={(value)=>{
                                                this.state.nameValue = value;
                                                componentStore.update(this,this.state)
                                            }}
                                        >
                                        </InputItem>
                                    </div>
                                </div>

                            </li>
                            <li>
                                <div className="_left">
                                    <div className="_img">
                                        <img src={require('../../../image/icon/safe_shenfenzheng.png')} alt=""/>
                                    </div>
                                </div>
                                <div className="_right _rightLast">
                                    <div className="_right_C">
                                        <InputItem
                                            {...getFieldProps('user')}
                                            maxLength="18"
                                            type="text"
                                            placeholder=""
                                            onBlur={this.userBlur}
                                            error={this.state.userError}
                                            onFocus={this.userFocus}
                                            value={this.state.userValue}
                                            onChange={(value)=>{
                                                this.state.userValue = value;
                                                componentStore.update(this,this.state)
                                            }}
                                        >
                                        </InputItem>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div className="_left">
                                    <div className="_img">
                                        <img src={require('../../../image/icon/safe_bank.png')} alt=""/>
                                    </div>
                                </div>
                                <div className="_right">
                                    <div className="_right_A">
                                        <Picker
                                            data={this.state.data}
                                            cols={this.state.cols}
                                            value={this.state.asyncValue}
                                            extra=""
                                            onPickerChange={this.onPickerChange.bind(this)}
                                        >
                                            <List.Item  onClick={this.onClick.bind(this)}>
                                            </List.Item>
                                        </Picker>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div className="_left">
                                    <div className="_img">
                                        <img src={require('../../../image/icon/safe_bank.png')} alt=""/>
                                    </div>
                                </div>
                                <div className="_right _rightLast">
                                    <div className="_right_B">
                                        <InputItem
                                            {...getFieldProps('bank')}
                                            maxLength="19"
                                            type="number"
                                            placeholder=""
                                            error={this.state.bankNumberError}
                                            onBlur={this.bankBlur}
                                            onFocus={this.banFocus}
                                            value={this.state.bankNumber}
                                            onChange={(value)=>{
                                                this.state.bankNumber = value;
                                                componentStore.update(this,this.state)
                                            }}
                                        >
                                        </InputItem>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <div className="safe_btm">
                        <p> </p>
                        {/*<Button content="" click={this.safeClick} $id="safe" width="90%" lineH={0.85+'rem'} radius="8px" height={0.85+'rem'}></Button>*/}
                        <div className="safe_btn">
                            <Button content="" click={this.safeClick} $id="safe" width="90%" lineH={0.85+'rem'} radius="8px" height={0.85+'rem'}></Button>
                        </div>
                    </div>
                </div>

             </div>
        )
    }

}
//const BasicInputExampleWrapper = createForm()(Forms);
const Global_Form = createForm()(Forms);
export default Global_Form;



